body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

nav {
  color: #fff;
  background-color: #090b18;
  padding: 1rem;
}

nav a {
  color: #fff;
  margin-right: 20px;
  text-decoration: none;
}

section {
  z-index: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
  position: relative;
  overflow: hidden;
}

video {
  z-index: -1;
  object-fit: fill;
  width: 100%;
  min-width: 100%;
  height: auto;
  min-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.content-overlay {
  text-align: left;
  color: #fff;
  z-index: 2;
  backdrop-filter: blur(30px);
  background: #0b0e1405;
  border-radius: 10px;
  padding: 2rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-80%, -50%);
}

.content-overlay h1 {
  margin-bottom: 1rem;
  font-size: 3rem;
}

.content-overlay button {
  color: #090909;
  cursor: pointer;
  background-color: #ebedf8;
  border: none;
  padding: 1rem 2rem;
  font-size: 1rem;
  transition: background-color .3s;
}

.content-overlay button:hover {
  background-color: #1a1e33;
}
